import { Route, Routes } from "react-router-dom";
import { useEffect } from "react";
import "./App.css";
//reduce
import { useDispatch } from "react-redux";
import { clearUser, loginUser } from "./Reducer/userSlice";
//firebase
import firebase from "./firebase";

import Heading from "./Component/Heading";
import Upload from "./Component/Post/Upload";
import Edit from "./Component/Post/Edit";
import Login from "./Component/User/Login";
import Register from "./Component/User/Register";
import PostArea from "./Component/Post/PostArea";
import MyPage from "./Component/User/MyPage";
import MainPage from "./Component/MainPage";

// //   
// import ReactPlayer from "react-player";

import "./Component/assets/Landingpage.css";

// import yousolo from "./Component/assets/olnyphYousolo.mp4";
// import tv from "./Component/assets/tv.mp4";
// import cloud from "./Component/assets/cloud.mp4";
import moldives from "./Component/assets/moldives.mp4";

function App() {
  const dispatch = useDispatch();
  // console.log(user.displayName);
  useEffect(() => {
    //onAuthStateChanged      ..
    firebase.auth().onAuthStateChanged((userInfo) => {
      //photoURL img ..firebase.auth
      // console.log(userInfo._delegate.photoURL);

      //  or    -> null
      //   
      // console.log(" : ", userInfo);
      if (userInfo !== null) {
        dispatch(loginUser(userInfo.multiFactor.user));
      } else {
        dispatch(clearUser());
      }
    });
  }, []);

  // useEffect(() => {
  //   console.log("user : ", user);
  // }, [user]);

  // useEffect(() => {
  //   //signOut() firebase   
  //   firebase.auth().signOut();
  // }, []);

  return (
    <>
      <div className="landingpage">
        <video src={moldives} autoPlay muted loop class="video-bg" />
        <div className="bg-overlay">
          <Heading />
          <Routes>
            <Route path="/" element={<MainPage />} />
            {/* mongoose  post, Reple  */}
            <Route path="/upload" element={<Upload />} />
            <Route path="/post/:postNum" element={<PostArea />} />
            <Route path="/edit/:postNum" element={<Edit />} />
            {/* firebase Auth() login */}
            <Route path="/login" element={<Login />} />
            <Route path="/register" element={<Register />} />
            {/* 
        Add
        1.  -(post)
        2.   &  
        3.   /  - (post)
      */}
            <Route path="/mypage" element={<MyPage />} />
          </Routes>
        </div>
      </div>
    </>
  );
}

export default App;
