import { Link, useNavigate, useParams } from 'react-router-dom';
//  
// import Loading from './../assets/Spinner';
import { BtnDiv, Post, PostDiv } from '../../Style/PostDetailCSS';
import { useSelector } from 'react-redux';
import Avatar from 'react-avatar';
import axios from 'axios';

import moment from 'moment';
import 'moment/locale/ko';

const Detail = (props) => {
  const navigate = useNavigate();
  const user = useSelector((state) => state.user);
  // console.log(user.photoURL);
  //  id ..
  const params = useParams();

  const SetTime = (a, b) => {
    if (a !== b) {
      return moment(b).format('YYYY MMMM Do hh:mm a') + '()';
    } else {
      return moment(a).format('YYYY MMMM Do hh:mm a');
    }
  };

  const deleteHandler = () => {
    if (window.confirm(' ?')) {
      // console.log(params.postNum); //post id ex)1
      let body = {
        //let body =
        postNum: params.postNum,
      };
      axios
        .post('/api/post/delete', body)
        .then((res) => {
          if (res.data.success) {
            alert(' .');
            navigate('/');
          }
        })
        .catch((err) => {
          alert('  .');
        });
    }
  };

  return (
    <PostDiv>
      <Post>
        <h2>{props.postDetil.title}</h2>
        <div className="author">
          <Avatar
            style={{
              background: 'rgb(232, 232, 232)',
              border: '1px solid rgb(210, 210, 210)',
            }}
            size="40"
            round={true}
            src={user.photoURL}
          />
          {props.postDetil.author.displayName}
          <p className="time">
            {SetTime(props.postDetil.createdAt, props.postDetil.updatedAt)}
          </p>
        </div>
        {props.postDetil.image ? (
          <img
            //     
            // src={`http://localhost:5000/${postDetil.image}`}
            src={props.postDetil.image}
            alt=""
            style={{ width: '100%', height: 'auto' }}
          />
        ) : null}
        <p>{props.postDetil.content}</p>
        {/* <p> {props.postDetil.repleNum}</p> */}
      </Post>
      {/* uid      on */}
      {user.uid === props.postDetil.author.uid && (
        <BtnDiv>
          <Link to={`/edit/${props.postDetil.postNum}`}>
            <button className="edit"></button>
          </Link>
          <Link>
            <button onClick={deleteHandler} className="delete">
              
            </button>
          </Link>
        </BtnDiv>
      )}
    </PostDiv>
  );
};

export default Detail;
