import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { MyPageDiv } from '../../Style/UserCSS.js';
import Avatar from 'react-avatar';
import axios from 'axios';
import firebase from '../../firebase.js';

const MyPage = () => {
  const user = useSelector((state) => state.user);
  const [currentImage, setCurrentImage] = useState('');
  const navigate = useNavigate();
  //   console.log(user.photoURL);
  useEffect(() => {
    //  Login  user.isLoading 
    if (user.isLoading && !user.accessToken) {
      navigate('/login');
    } else {
      setCurrentImage(user.photoURL);
    }
  }, [user]);

  const ImageUpload = (e) => {
    // console.log(e.target.files[0]);
    var formData = new FormData(); //{}
    formData.append('file', e.target.files[0]); //XMLHtpRequest
    // formData    // {}
    //for   ..
    // for (const keyValu of formData) console.log(keyValu);
    axios
      .post('/api/user/profile/image', formData)
      .then((res) => {
        // console.log(res.data);
        setCurrentImage(res.data.filePath);
      })
      .catch(() => {});
  };

  const saveProfile = async (e) => {
    e.preventDefault();

    try {
      await firebase.auth().currentUser.updateProfile({
        //user  
        photoURL: currentImage,
      });
    } catch (err) {
      return alert('  .');
    }
    let body = {
      photoURL: currentImage,
      uid: user.uid,
    };
    axios.post('/api/user/profile/update', body).then((res) => {
      if (res.data.success) {
        alert('  .');
        window.location.reload();
      } else {
        alert('  .');
      }
    });
  };

  return (
    <MyPageDiv style={{ width: '100vw', height: '100vh' }}>
      <form
        style={{
          width: '50%',
          margin: '0 auto',
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          marginTop: '2rem',
        }}
      >
        <label>
          <input
            type="file"
            accept="image/*"
            style={{ display: 'none' }}
            onChange={ImageUpload}
          />
          <Avatar
            style={{
              background: 'rgb(232, 232, 232)',
              border: '1px solid rgb(210, 210, 210)',
              cursor: 'pointer',
            }}
            size="100"
            round={true}
            src={currentImage}
          />
        </label>
        <button onClick={saveProfile}></button>
      </form>
    </MyPageDiv>
  );
};

export default MyPage;
